import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators, FormArray } from '@angular/forms';
import { Router, ActivatedRoute, Params } from '@angular/router';

import { UserService } from 'src/app/services/user.service';
import { BidActionService } from 'src/app/services/bid-action.service';
import { bidActionModel } from 'src/app/model/bidActionModel';
import { Subscription } from 'rxjs';
import { bidTaskModel } from 'src/app/model/bidTaskModel';

@Component({
  selector: 'app-bid-edit',
  templateUrl: './bid-edit.component.html',
  styleUrls: ['./bid-edit.component.css']
})
export class BidEditComponent implements OnInit {
  bidForm : FormGroup;
  currentBid: bidActionModel;
  minimumBid: number;
  selectedBidIndex: number;
  bidsChangedSub: Subscription;
  isSeller: Boolean;
  previousBid: number;

  constructor(private bidActionService: BidActionService,
              private activeRoute: ActivatedRoute,
              private router: Router) { }

  ngOnInit(): void {
    this.activeRoute.params.subscribe(
      (params: Params) => {
        this.selectedBidIndex = +params['taskID'];
        this.isSeller = (params['isSeller']==='true');

        this.FormInit();
      }
    );
    this.FormInit();
  }

FormInit() {
  this.currentBid = this.bidActionService.bidTasks[this.selectedBidIndex];

  if (this.currentBid.AgreedCostOfItem && !this.isSeller) {
    this.currentBid.BidAmount = this.currentBid.AgreedCostOfItem;
  } else {
    if (this.currentBid.BidAmount > 0 && !this.isSeller) {
      this.previousBid = this.currentBid.BidAmount;
      this.currentBid.BidAmount = null;
    }
  }
  // minimum bid is 10% below value
  this.minimumBid = this.currentBid.Value - (this.currentBid.Value * .10);
  this.currentBid.Action = this.currentBid.Actions[0];
  this.currentBid.BidAmount;
  this.bidForm = new FormGroup({
    'Seller': new FormControl(this.currentBid.SellerName),
    'Item': new FormControl(this.currentBid.Item),
    'Value': new FormControl(this.currentBid.Value),
    'Weight': new FormControl(this.currentBid.Weight),
    'Bid': new FormControl(this.currentBid.BidAmount,
                            [
                              Validators.required,
                              Validators.min(this.minimumBid)
                            ]),
    'CounterBid': new FormControl(this.currentBid.CounterBidAmount,
                            [
                              Validators.required,
                              Validators.min(this.minimumBid)
                            ]),
    'Action': new FormControl(this.currentBid.Action,[Validators.required])
  });
  this.bidForm.controls['Bid'].disable();
  this.bidForm.controls['CounterBid'].disable();

  if (this.isSeller) {
    if (this.currentBid.Action==='CounterBid') {
      this.bidForm.controls['CounterBid'].enable();  // will be enabled if counter bid action selected
    }
  } else {
    if (this.currentBid.Action==='Bid') {
      this.bidForm.controls['Bid'].enable();
    }
  }

  this.bidForm.controls['Action'].valueChanges.subscribe( action => {
    if (action==='CounterBid') {
      this.bidForm.controls['CounterBid'].enable();
    } else {
      this.bidForm.controls['CounterBid'].disable();
    }
    if (action==='Bid') {
      this.bidForm.controls['Bid'].enable();
    } else {
      this.bidForm.controls['Bid'].disable();
    }
  });
}

onSubmit() {
    this.currentBid.BidAmount = this.bidForm.controls['Bid'].value;
    this.currentBid.CounterBidAmount = this.bidForm.controls['CounterBid'].value;
    this.currentBid.Action = this.bidForm.controls['Action'].value;

    this.bidActionService.updateBid(this.currentBid)
      .then( (resp) => {
        this.bidActionService.bidTasks.splice(this.selectedBidIndex,1);
        this.bidActionService.bidActionsChanged.next(this.bidActionService.bidTasks);
        this.onCancel();
      },
      (err) => {
        alert(err.error.summary);
      });
  }

  onCancel() {
    const link = this.isSeller ? '/seller' : '/buyer';
    this.router.navigate([link]);
  }
}
