import { Injectable, OnInit } from '@angular/core';
import { userModel } from '../model/userModel';
import { Subject } from 'rxjs';
import { DataService } from './data.service';

@Injectable()

export class UserService {
  UsersChanged = new Subject<userModel[]>();
  selectedSeller: number = -1;
  selectedBuyer: number = -1;

  users: userModel[] = [
    // new userModel(1,"seller","Rich",true,"Rich Taylor","abc@somewhere.org"),
    // new userModel(2,"buyer","Qing",true,"Qing Xu","person@myco.cn"),
    // new userModel(3,"buyer","Robert",true,"Robert Smith","rsmith@dealersareus.xx"),
    // new userModel(4,"seller","George",true,"George Patton","bloodandguts@army.xx")
  ];

  constructor(private dataService: DataService) { }

  selectUser(userID: number) {
  }

  getUsers(usertype: string) {
    return this.getUsersByType(usertype);
  }

  getUserContact(userID:number) {
    return this.users.find(user => userID === userID).contact;
  }

  getUsersByType(usertype: string) {
    if (!this.users.length) {
      this.fetchUsers();
    }
    return this.users.filter(user => user.type == usertype);
  }

  fetchUsers() {
    this.dataService.getActiveUsers().subscribe(response => {
      this.users = response;
      this.UsersChanged.next(this.users);
    });
  }
}
