import { Injectable, OnInit } from '@angular/core';
import { Subject } from 'rxjs';
import { itemModel } from '../model/itemModel';
import { DataService } from './data.service';

@Injectable()

export class ItemService implements OnInit {
  itemsChanged = new Subject<itemModel[]>();
  retStatus: any;

  activeID: string;

  items: itemModel[] = [
    // new itemModel("1",1,"Rich Taylor",new Date("06/01/2020"),"Pending",
    //               333.77,"Test Item 1",23.5,"Milford","CT"),
    // new itemModel("2",4,"George Patton",new Date("05/15/2020"),"Pending",
    //               44.55,"Test Item 2",15.5,"Balitmore","MD")
  ];

  constructor(private dataService: DataService) { }

  ngOnInit(): void {

  }

  getItems() {
    return this.items;
  }

  getItem(itemID: string) {
    return this.items[itemID];
  }

  getItemsForUser(userID: number) {
    // return this.items.filter(item => item.sellerID == userID)
    return this.dataService.fetchItems(userID,'Pending').subscribe( items => {
      this.items = items;
      this.itemsChanged.next(items);
    });
  }

addItem(newItem:any) {
    return this.dataService.postItem(newItem).toPromise();
  }
}
