import { Component, OnInit, Injectable, OnDestroy } from '@angular/core';
import { Subject, Subscription } from 'rxjs';

import { userModel } from '../model/userModel';
import { UserService } from '../services/user.service';
import { ItemService } from '../services/item.service';
import { itemModel } from '../model/itemModel';
import { Router } from '@angular/router';
import { BidActionService } from '../services/bid-action.service';
import { BidService } from '../services/bid.service';

@Injectable()

@Component({
  selector: 'app-seller',
  templateUrl: './seller.component.html',
  styleUrls: ['./seller.component.css'],
  providers: [ItemService, BidService, BidActionService]
})
export class SellerComponent implements OnInit, OnDestroy {
  workListChanged = new Subject<any>();

  sellers: userModel[];
  selectedSeller: number;
  sellerItems: itemModel[];
  usersChangedSub: Subscription

  constructor(private userService: UserService,
              private itemService: ItemService,
              private bidActionService: BidActionService,
              private router: Router) { }

  ngOnInit(): void {
    this.userService.fetchUsers();

    this.usersChangedSub = this.userService.UsersChanged.subscribe(users => {
      this.sellers = users.filter(user => user.type == 'seller');
    });
    this.selectedSeller = this.userService.selectedSeller;
    if (this.selectedSeller != -1) {
      this.itemService.getItemsForUser(this.selectedSeller);
      this.bidActionService.getBidsForUser(this.selectedSeller, 'seller');
    }
 }

  ngOnDestroy() {
    this.usersChangedSub.unsubscribe();
  }

  onChangeSeller() {
    this.itemService.getItemsForUser(this.selectedSeller);
    this.userService.selectedSeller = this.selectedSeller;
    this.bidActionService.getBidsForUser(this.selectedSeller, 'seller');
    this.router.navigate(['/seller']);
  }
}
