import { Injectable } from '@angular/core';
import { Subject } from 'rxjs';
import { bidActionModel } from '../model/bidActionModel';
import { DataService } from './data.service';
import { UserService } from './user.service';
import { isDefined } from '@angular/compiler/src/util';

// @Injectable({
//   providedIn: 'root'
// })
@Injectable()

export class BidActionService {
  bidActionsChanged = new Subject<bidActionModel[]>();

  activeBid: string;

  bidTasks: bidActionModel[] = [];

  constructor(private dataService: DataService,
              private userService: UserService) { }

  getBidsForUser(userID: number,userType: String) {
    const filteredUsers = this.userService.users.filter(user => user.userID === userID &&
                                                        user.type === userType)
    const userName = filteredUsers[0].contact;
    this.bidTasks.length = 0;
    this.dataService.fetchBidTasks(userName).subscribe(response => {
      response.forEach( task => {
        const sellerUser = this.userService.users.filter(user => user.userID == task.Sellerid  &&
                                                        user.type === 'seller')[0];
        task.SellerName = isDefined(sellerUser) ? sellerUser.name : '';
        this.bidTasks.push(task);
      });
      this.bidActionsChanged.next(this.bidTasks);
    });


  }


  /* bid update PUT payload
  {
	"Action":"Bid",
    "FormValues": {
      "Bid Amount":425,
      "Counter Bid Amount":533
  }
  }
  */
  updateBid(bidAction: bidActionModel) {
    let formValues = {
      "Bid Amount": bidAction.BidAmount,
      "Counter Bid Amount": bidAction.CounterBidAmount
    };

    let bidMessage = {
      "Action": bidAction.Action,
      "FormValues": formValues
    };
    return this.dataService.updateBid(bidAction.ID,bidMessage).toPromise();
  }

}
