import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { DefaultPageComponent } from './default-page/default-page.component';
import { SellerComponent } from './seller/seller.component';
import { ItemBidsComponent } from './item-list/item-bids/item-bids.component';
import { BuyerComponent } from './buyer/buyer.component';
import { ItemEditComponent } from './item-list/item-edit/item-edit.component';
import { BidEditComponent } from './bid-tasks/bid-edit/bid-edit.component';


const routes: Routes = [
  {path: '', component: DefaultPageComponent, pathMatch: 'full'},
  {path: 'seller', component: SellerComponent,
    children: [

      {path: 'item/bids/:itemID', component: ItemBidsComponent},
      {path: 'item/new', component: ItemEditComponent},
      {path: 'bidtask/:taskID', component: BidEditComponent}
    ]},
  {path: 'buyer', component: BuyerComponent,
    children: [
      {path: 'bidtask/:taskID', component: BidEditComponent}
    ]}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
