import { Injectable } from '@angular/core';
import { itemBidThreadModel } from '../model/itemBidThreadModel';
import { Subject } from 'rxjs';
import { DataService } from './data.service';

@Injectable()

export class BidService {
  bidsChanged = new Subject<itemBidThreadModel[]>();

  constructor(private dataService: DataService) { }

  getBids(ItemID: number) {
    return this.dataService.fetchItemBids(ItemID).subscribe( itemBids => {
      this.bidsChanged.next(itemBids);
    })

  }
}
