import { ActivatedRoute, Params } from '@angular/router';
import { Component, OnInit, OnDestroy } from '@angular/core';

import { itemBidThreadModel } from 'src/app/model/itemBidThreadModel';
import { BidService } from 'src/app/services/bid.service';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-item-bids',
  templateUrl: './item-bids.component.html',
  styleUrls: ['../../shared/table.css','./item-bids.component.css']
})
export class ItemBidsComponent implements OnInit, OnDestroy {
  bids: itemBidThreadModel[];
  itemID: number;
  bidsChangedSub: Subscription;

  constructor(private BidsService: BidService,
              private activeRoute: ActivatedRoute) { }

  ngOnInit(): void {
    this.bids = [];
    this.bidsChangedSub = this.BidsService.bidsChanged.subscribe( bids => {
      this.bids = bids.filter(bid => bid.status != '' && bid.status != null);
    })
    this.activeRoute.params.subscribe(
      (params: Params) => {
        this.itemID = +params['itemID'];
        this.BidsService.getBids(this.itemID);
      }
    );
  }

  ngOnDestroy() {
    this.bidsChangedSub.unsubscribe();
  }
}
