import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, FormArray, Validators } from '@angular/forms';

import { ItemService } from 'src/app/services/item.service';
import { UserService } from 'src/app/services/user.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-item-edit',
  templateUrl: './item-edit.component.html',
  styleUrls: ['./item-edit.component.css']
})
export class ItemEditComponent implements OnInit {
  itemForm: FormGroup;
  sellerID: number = this.userService.selectedSeller;

  constructor(private itemService: ItemService,
              private userService: UserService,
              private router: Router) { }

  ngOnInit(): void {
    const AddrFormGroup = new FormGroup({
      'street': new FormControl(''),
      'city': new FormControl(''),
      'state': new FormControl(''),
      'zip': new FormControl(''),
      'country': new FormControl('USA'),
    })
    this.itemForm = new FormGroup({
      'description': new FormControl('',Validators.required),
      'value': new FormControl(null,[
                              Validators.required,
                              Validators.min(1),
                              Validators.max(99999)
                            ]),
      'weight': new FormControl(null,[
                              Validators.required,
                              Validators.min(1),
                              Validators.max(400)
                            ]),
      'Address': AddrFormGroup
    });

  }

  onSubmit() {
    let newItem = this.itemForm.value;
    newItem.seller = this.sellerID;

    this.itemService.addItem(newItem)
      .then( (resp) => {
        this.itemService.items.push(resp);
        this.itemService.itemsChanged.next(this.itemService.items);
        this.onCancel();
      },
      (err) => {
        alert(err.error.summary);
      });

  }

  onCancel() {
    this.router.navigate(['/seller'])
  }
}
