import { Component, OnInit, OnDestroy, Injectable } from '@angular/core';
import { UserService } from '../services/user.service';
import { userModel } from '../model/userModel';
import { ItemService } from '../services/item.service';
import { Subscription, Subject } from 'rxjs';
import { Router } from '@angular/router';
import { BidActionService } from '../services/bid-action.service';

@Injectable()

@Component({
  selector: 'app-buyer',
  templateUrl: './buyer.component.html',
  styleUrls: ['./buyer.component.css'],
  providers: [ItemService ,BidActionService]
})
export class BuyerComponent implements OnInit, OnDestroy {

  buyers: userModel[];
  selectedBuyer: number;
  usersChangedSub: Subscription;

  constructor(private UserService: UserService,
              private bidActionService: BidActionService,
              private router: Router) { }

  ngOnInit(): void {
    this.UserService.fetchUsers();

    this.usersChangedSub = this.UserService.UsersChanged.subscribe(users => {
      this.buyers = users.filter(user => user.type == 'buyer');
    });
    this.selectedBuyer = this.UserService.selectedBuyer;
    if (this.selectedBuyer != -1) {
      this.bidActionService.getBidsForUser(this.selectedBuyer, 'buyer');
    }
  }

  ngOnDestroy() {
    this.usersChangedSub.unsubscribe();
  }

  onChangeBuyer() {
    this.UserService.selectedBuyer = this.selectedBuyer;
    if (this.selectedBuyer != -1) {
      this.bidActionService.getBidsForUser(this.selectedBuyer, 'buyer');
    }
    this.router.navigate(['/buyer']);
  }
}
