import { Component, OnInit, OnDestroy, Input } from '@angular/core';
import { BidActionService } from '../services/bid-action.service';
import { bidActionModel } from '../model/bidActionModel';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-bid-tasks',
  templateUrl: './bid-tasks.component.html',
  styleUrls: ['../shared/table.css','./bid-tasks.component.css']
})
export class BidTasksComponent implements OnInit, OnDestroy {

  workList: bidActionModel[] = []
  bidTaskSubscription: Subscription;
  @Input() isSeller: boolean = true;

  constructor(private bidActionService: BidActionService) { }

  ngOnInit(): void {
    this.bidTaskSubscription = this.bidActionService.bidActionsChanged.subscribe( (worklist: bidActionModel[]) => {
      this.workList = worklist;
    })
  }

  ngOnDestroy() {
    this.bidTaskSubscription.unsubscribe();
  }

}
