import { BrowserModule } from '@angular/platform-browser';
import { NgModule, APP_INITIALIZER } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { HttpClientModule } from '@angular/common/http';

import { DefaultPageComponent } from './default-page/default-page.component';
import { SellerComponent } from './seller/seller.component';
import { BuyerComponent } from './buyer/buyer.component';
import { ItemEditComponent } from './item-list/item-edit/item-edit.component';
import { ItemListComponent } from './item-list/item-list.component';

import { BidEditComponent } from './bid-tasks/bid-edit/bid-edit.component';
import { ItemBidsComponent } from './item-list/item-bids/item-bids.component';
import { ItemRowComponent } from './item-list/item-row/item-row.component';
import { FilterPipe } from './filter.pipe';
import { BidTasksComponent } from './bid-tasks/bid-tasks.component';
import { BidTaskRowComponent } from './bid-tasks/bid-task-row/bid-task-row.component';
import { UserService } from './services/user.service';
import { AppConfig } from './app.config';

export function initializeApp(appConfig: AppConfig) {
  return () => appConfig.load();
}

@NgModule({
  declarations: [
    AppComponent,
    DefaultPageComponent,
    SellerComponent,
    BuyerComponent,
    ItemEditComponent,
    ItemListComponent,
    BidEditComponent,
    ItemBidsComponent,
    ItemRowComponent,
    FilterPipe,
    BidTasksComponent,
    BidTaskRowComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    ReactiveFormsModule,
    HttpClientModule,
    AppRoutingModule
  ],
  providers: [
    UserService,
    AppConfig,
      {
        provide: APP_INITIALIZER,
        useFactory: initializeApp,
        deps: [AppConfig],
        multi: true
      }
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }
