import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { IAppConfig } from './app-config.model';

@Injectable()

export class AppConfig {
  static config: IAppConfig;

  constructor(private http: HttpClient) { }

  load() {
    const configFile = 'assets/config.json';

    return new Promise<void>((resolve, reject) => {
      this.http.get(configFile).toPromise().then((response: IAppConfig) => {
        AppConfig.config = <IAppConfig>response;
        resolve();
      }).catch((response: any) => {
        reject('Could not load config file "assets/config.json"');
      });
    });
  }
}
