import { Injectable } from '@angular/core';
import { HttpClient, HttpParams } from '@angular/common/http';
import { map } from 'rxjs/operators';

import { userModel } from '../model/userModel';
import { itemModel } from '../model/itemModel';
import { itemBidThreadModel } from '../model/itemBidThreadModel';
import { bidTaskModel } from '../model/bidTaskModel';
import { bidActionModel } from '../model/bidActionModel';
import { AppConfig } from '../app.config'

@Injectable({
  providedIn: 'root'
})
export class DataService {
  // private serveraddr = "http://192.169.250.2:52773";
  // private serveraddr = "http://"+AppConfig.config.ip+":52773";
  private serveraddr = "http://"+AppConfig.config.ip+":"+AppConfig.config.port;

  constructor(
    private http: HttpClient
  ) { }

  getActiveUsers() {
    console.log(AppConfig.config);
    console.log('server='+this.serveraddr);
    let reqParams = new HttpParams().set('Active','1');

    return this.http.get<userModel[]>(this.serveraddr+'/bestoffer/api/users',{params: reqParams})
  }

  fetchItems(user: number = null, status:string = null)  {
    let reqParams = new HttpParams();
    if (user != null) {
      reqParams = reqParams.append('seller', user.toString());
    }
    if (status!=null) {
      reqParams = reqParams.append('status', status);
    }

    return this.http.get<itemModel>(this.serveraddr+'/bestoffer/api/items',
      {
        params: reqParams
      })
      .pipe(
        map(responseData => {
          const itemsArray: itemModel[] = [];
          for (const key in responseData) {
            let item:itemModel = new itemModel(
              responseData[key].ID,
              responseData[key].SellerId,
              responseData[key].Seller.name,
              responseData[key].InitialDate,
              responseData[key].Status,
              responseData[key].Value,
              responseData[key].Description,
              responseData[key].Weight,
              responseData[key].OriginationAddress.City,
              responseData[key].OriginationAddress.State
            );
            itemsArray.push(item);
          }

          return itemsArray;
      }));
  }

  fetchItemBids(itemId: number) {
    let reqParams = new HttpParams().set('item',itemId.toString());

    return this.http.get<itemBidThreadModel>(this.serveraddr+'/bestoffer/api/items/bids',
      {
        params: reqParams
      })
      .pipe(
        map(responseData => {
          const bidsArray: itemBidThreadModel[] = [];
          for (const key in responseData) {
            let itemBids:itemBidThreadModel = new itemBidThreadModel(
              responseData[key].ID,
              responseData[key].BidAmount,
              responseData[key].BuyerID,
              responseData[key].FinalBidAmount,
              responseData[key].ItemID,
              responseData[key].Status,
              responseData[key].Buyer.name
            );
            bidsArray.push(itemBids);
          }
          return bidsArray;
      }));

  }

  postItem(item:any) {
    return this.http.post<any>(this.serveraddr+'/bestoffer/api/items/new',item)
    .pipe(
      map(responseData => {
        let item:itemModel = new itemModel(
          responseData.ID,
          responseData.SellerId,
          responseData.Seller.name,
          responseData.InitialDate,
          responseData.Status,
          responseData.Value,
          responseData.Description,
          responseData.Weight,
          responseData.OriginationAddress.City,
          responseData.OriginationAddress.State
        );
        return item;
    }));

  }

  fetchBidTasks(userName: string) {
    const url = this.serveraddr+'/bestoffer/api/bids/'+userName
    const request = this.http.get<bidActionModel[]>(url);
    return request;

  }

  updateBid(bidID: string, bidMessage:any) {
    const url = this.serveraddr+'/bestoffer/api/bids/'+bidID;

    return this.http.put<any>(url,bidMessage);
  }
}
